* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #F1F6EC;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Unbounded', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card, .card-pop {
  animation: all 0.2s ease-out;
}

.card:hover, .card-pop:hover {
  border: 4px solid #7ECC66;
  background-color: #7ECC66;
  border-radius: 2.4rem;
}

.category-btn {
  animation: all 0.2s ease-out;
}

.category-btn:hover {
  opacity: 0.8;
}

.c-hove {
  animation: all 0.4s ease;
}

.c-hove:hover {
  font-size: 1.1rem;
  animation: all 0.4s ease;
}

a {
  color: #1F1F1F;
  text-decoration: none;
  margin: 0 0.5rem;
  text-align: center;
}

@media (min-width: 320px) {
  .card, .card-pop {
    height: 6rem;
    width: 6rem;
  }

  .recipe-title {
    font-size: 0.5rem;
  }

  span {
    font-size: 2rem;
    margin: 0;
  }

  .category-ctn {
    margin: 0;
  }

  .category-btn {
    width: 3.2rem;
    height: 3.2rem;
  }

  .category-btn span {
    font-size: 0.4rem;
  }

  .slide-container {
    margin: 0;
    padding: 0 1rem 4rem 1rem;
  }

  .slide-container h2 {
    font-size: 0.9rem;
  }

  .search-form {
    margin: 0 1rem;
  }

  body {
    width: 100%;
    margin: 0;
    padding: 0;
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 920px) {
  .card, .card-pop {
    height: 14rem;
    width: 14rem;
  }

  span {
    font-size: 2.4rem;
  }

  .recipe-title {
    font-size: 1rem;
  }

  .category-btn {
    width: 5.5rem;
    height: 5.5rem;
  }

  .category-btn span {
    font-size: 0.8rem;
  }

  .slide-container h2 {
    font-size: 1.2rem;
  }


  body {
    margin: 0;
    padding: 0;
    max-width: 100%;
  }


}